useGLTF.preload("/2021/model-transformed.glb");

function SpriteHover(props) {
  const spriteRef = useRef();
  useFrame(() => {
    if (props.object && spriteRef) {
      spriteRef.current.position.x = props.object.position.x / 25;
      spriteRef.current.position.z = props.object.position.z / 25 - 0.5;
    }
  });

  return (
    <group ref={spriteRef} visible={false}>
      <Html
        sprite
        transform
        position={[0, 0.4, 0]}
        style={{
          background: "white",
          fontSize: "6px",
          padding: "2px 5px",
          borderRadius: "25px",
          display: props.object ? "block" : "none",
        }}
      >
        {props.object ? props.object.name.replace(/\_/g, " ") : null}
      </Html>
    </group>
  );
}
